import React from "react";
import "./css/NewsVideoSmall.css";
export const NewsVideoSmall = (props) => {
  const linked_url = props.news.url;
  const image_url =
    "https://images.thequint.com/" + props.news["hero-image-s3-key"];
  const headline = props.news.headline;

  // <a href={linked_url}>
  //       <div className="card-image-wrapper">
  //         <figure className="image-figure">
  //           <picture>
  //             <img src={image_url} className="small-newsImage"></img>
  //           </picture>
  //         </figure>
  //       </div>
  //     </a>
  //     <a href={linked_url} className="headline-link">
  //       <h2 className="headline-type-10">{headline}</h2>
  //     </a>
  return (
    <div className="card-grid-wrapper">
      <div className="card--image-wrapper">
        <a href={linked_url}>
          <figure className="image-figure">
            <picture>
              <img src={image_url} className="small-newsImage"></img>
            </picture>
          </figure>
        </a>
        <a href={linked_url} className="headline-link">
          <h2 className="headline-type-10">{headline}</h2>
        </a>
      </div>
    </div>
  );
};
