import React from "react";
import { useParams } from "react-router-dom";
import { StoryMain } from "./StoryMain";
import "./css/InnerStory.css";
import { Subscribe } from "./Subscribe";
export const InnerStory = (props) => {
  const { id } = useParams();
  console.log("Innestroy ");
  console.log(props);
  const news_data = props.section_1[id];
  console.log(news_data);
  return (
    <div className="container root-container">
      <div className="grid-col-1">
        <StoryMain news={news_data}></StoryMain>
      </div>
      <div className="grid-col-2">
        <div className="add-wraper">
          <span className="add-heading">ADVERTISEMENT</span>
          <div className="add-box">
            <div className="add"></div>
          </div>
        </div>
        <Subscribe></Subscribe>
      </div>
    </div>
  );
};
