import React from "react";

export const VideoNewsCol1 = (props) => {
  // console.log("Inside VideNewsClo1 component");
  // console.log(props);
  const linked_url = props.news.url;
  const image_url =
    "https://images.thequint.com/" + props.news["hero-image-s3-key"];
  const headline = props.news.headline;
  return (
    <>
      <a href={linked_url}>
        <div className="video-news-card">
          <figure className="card-image-figure">
            <picture>
              <img src={image_url} className="news-image"></img>
            </picture>
          </figure>
          <div className="card-image-headline container">
            <h2 className="headline-type-5">{headline}</h2>
          </div>
        </div>
      </a>
    </>
  );
};
