import React from "react";
import "./css/Explainers.css";
import { ExpMainCard } from "./ExpMainCard";
import { ExpSnallCard } from "./ExpSnallCard";
export const Explainers = (props) => {
  return (
    <div className="container explainer-container">
      <div className="explainers-headig-container">
        <h1 className="headline headline-explainer">Explainers</h1>
      </div>
      <div className="exp-grid">
        <ExpMainCard news={props.news[0]}></ExpMainCard>
        <div className="col__2_grid">
          <ExpSnallCard news={props.news[1]}></ExpSnallCard>
          <ExpSnallCard news={props.news[2]}></ExpSnallCard>
          <ExpSnallCard news={props.news[3]}></ExpSnallCard>
        </div>
      </div>
    </div>
  );
};
