import React from "react";

export const StoryMain = (props) => {
  console.log("main Story");
  console.log(props.news.story.headline);
  const headline = props.news.story.headline;
  const author = props.news.story["author-name"];
  const image_url =
    "https://images.thequint.com/" + props.news.story["hero-image-s3-key"];
  const summary = props.news.story.summary;

  console.log(headline);
  return (
    <div clasName="story-head">
      <h1 className="headline-type-10">{headline}</h1>
      <p className="para">{summary}</p>
      <div className="section-timestamp">
        {/* flex */}
        <div className="left-sec">
          <div className="sec-head">{author}</div>
        </div>
        <div className="right-sec"></div>
      </div>
      <div className="card-image-wrapper">
        <div>
          <figure className="news-image-dynamic">
            <picture className="news-image">
              <img
                src={image_url}
                data-src={image_url}
                type="image/webp"
                className="photo"
                alt="news story "
              ></img>
            </picture>
          </figure>
        </div>
      </div>
    </div>
  );
};
