import React from "react";

export const ExpSnallCard = ({ news }) => {
  var linked_url = news.story.url;
  var image_url =
    "https://images.thequint.com/" + news.story["hero-image-s3-key"];
  const headline = news.story.headline;
  return (
    <>
      <a href="linked_url" className="small-card-grid">
        <div className="custom-story-card-wrapper">
          <figure className="image-figure">
            <div className="card-round-image">
              <img src={image_url} className="card-image-small"></img>
            </div>
          </figure>
        </div>
        <div className="custom-story-card-header-small">
          <h1 className="headline-type--14">{headline}</h1>
        </div>
      </a>
    </>
  );
};
