import React from "react";
import "./css/ExpMainCard.css";
export const ExpMainCard = ({ news }) => {
  var linked_url = news.story.url;
  var image_url =
    "https://images.thequint.com/" + news.story["hero-image-s3-key"];
  const headline = news.story.headline;
  return (
    <>
      <a href={linked_url} className="custom-story-card-10">
        <div className="image-heading-card">
          <div className="pri-card">Explained</div>
          <div className="sec-card">In 5 Cards</div>
        </div>
        <div className="custom-story-card-wrapper">
          <figure className="image-figure">
            <div className="card-round-image">
              <img src={image_url} className="card-image-big"></img>
            </div>
          </figure>
        </div>
        <div className="custom-story-card-header">
          <h1 className="headline-type--14">{headline}</h1>
        </div>
      </a>
    </>
  );
};
