import React from "react";
import request from "superagent";
import { useEffect, useState } from "react";
import { BigCard } from "./BigCard";
import { NewsTop } from "./NewsTop";
import { NewsTopCard } from "./NewsTopCard";
import { SmallCard } from "./SmallCard";
import loader from "../loading.gif";
import { RootVideoNews } from "./RootVideoNews";
import { Explainers } from "./Explainers";
import { Lens } from "./Lens";
export const Home = () => {
  const [loading, setLoading] = useState(true);
  const [news, setNews] = useState([]);
  const [videonews, setVideoNews] = useState([]);
  const [explainers, setExplainers] = useState([]);
  const [lens, setLens] = useState([]);
  const fetchData = async () => {
    const url = `https://www.thequint.com/api/v1/collections/home`;
    let rootResponse = await request.get(url).then((res) => res.body.items);
    let url_slug = rootResponse[0].slug;
    let url_slug2 = rootResponse[1].slug;
    let url_slug3 = rootResponse[12].slug;
    let url_slug4 = rootResponse[14].slug;
    try {
      const newsurl = `https://www.thequint.com/api/v1/collections/${url_slug}`;
      let newsResponse = await request
        .get(newsurl)
        .then((res) => res.body.items);
      const ourData = newsResponse;
      console.log(ourData);
      const newsurl2 = `https://www.thequint.com/api/v1/collections/${url_slug2}`;
      let newsResponse2 = await request
        .get(newsurl2)
        .then((res) => res.body.items);
      newsResponse2 = newsResponse2.filter((news, index) => index < 10);

      const newsurl3 = `https://www.thequint.com/api/v1/collections/${url_slug3}`;
      let newsResponse3 = await request
        .get(newsurl3)
        .then((res) => res.body.items);

      const newsurl4 = `https://www.thequint.com/api/v1/collections/${url_slug4}`;
      let newsResponse4 = await request
        .get(newsurl4)
        .then((res) => res.body.items);
      newsResponse4 = newsResponse4.filter((news, index) => index < 4);

      setNews(ourData);
      setVideoNews(newsResponse2);
      setExplainers(newsResponse3);
      setLens(newsResponse4);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    fetchData();
  }, []);
  if (loading) {
    return (
      <div className="">
        <div className="loading-container">
          <img
            src={loader}
            style={{ width: "50%", height: "30%", margin: "0 auto" }}
            alt=""
          ></img>
        </div>
      </div>
    );
  }
  return (
    <>
      <div className="container root-container">
        <div className="grid-col-1">
          <BigCard news={news[0]} />
          <div className="grid-col-row2">
            <div className="news-order">
              <NewsTop news={news[1]} id={1}></NewsTop>
              <NewsTop news={news[2]} id={2}></NewsTop>
              <NewsTopCard news={news[3]} id={3}></NewsTopCard>
            </div>
            <div className="grid-col-row2-right">
              <div className="news-order">
                <SmallCard news={news[4]} id={4}></SmallCard>
              </div>
            </div>
          </div>
        </div>
        <div className="grid-col-2">
          <div className="add-wraper">
            <span className="add-heading">ADVERTISEMENT</span>
            <div className="add-box">
              <div className="add"></div>
            </div>
          </div>
          <div className="right-column-news">
            <NewsTop news={news[5]} id={5}></NewsTop>
            <NewsTop news={news[6]} id={6}></NewsTop>
            <NewsTopCard news={news[7]} id={7}></NewsTopCard>
          </div>
        </div>
      </div>
      <RootVideoNews news={videonews}></RootVideoNews>
      <Explainers news={explainers}></Explainers>
      <Lens news={lens}></Lens>
    </>
  );
};
