import React from "react";
import "./css/RootVideoNews.css";

import { VideoNewsCol1 } from "./VideoNewsCol1";
import { NewsVideoSmall } from "./NewsVideoSmall";
export const RootVideoNews = (props) => {
  console.log(props);
  return (
    <div className="videonews-root-conatiner">
      <div className="root--container">
        <div className="root--container-grid">
          <VideoNewsCol1 news={props.news[0].story}></VideoNewsCol1>
          <div className="col2-2-grid disable-scrollbar">
            <NewsVideoSmall news={props.news[1].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[2].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[3].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[4].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[5].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[6].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[7].story}></NewsVideoSmall>
            <NewsVideoSmall news={props.news[8].story}></NewsVideoSmall>
          </div>
        </div>
      </div>
    </div>
  );
};
